h1 {
  color: #6065ac;
  text-align: center;
  margin-bottom: 15px;
  font-size: 1.3em;
  font-weight: bolder;
}

section {
  width: 80%;
  margin: 20px auto;
}

img {
  margin: 0 auto;
}

.card {
  width: 250px;
  text-align: center;
  height: auto;
  border: 2px solid gray;
  padding: 25px;
  font-size: 1rem;
}

.capitalize {
  text-transform: capitalize;
  color: #3b52a3;
}

.card-body {
  width: 200px;
  margin: 0 auto;
  padding: 1px;
}

.swiper-slide {
  width: 250px;
}

.swiper-slide-active {
  border-bottom: 5px solid #3b52a3;
}

.swiper-button-prev {
  left: var(--swiper-navigation-sides-offset, 5px);
  font-weight: bolder;
}

.swiper-button-next {
  right: var(--swiper-navigation-sides-offset, 5px);
  font-weight: bolder;
}

.swiper-button-prev:hover, .swiper-button-next:hover {
  color: #6065ac;
}

#autocomplete-list {
  width: 100%;
  max-height: 200px;
  z-index: 3;
  background-color: #fff;
  font-family: Inter;
  display: none;
  position: absolute;
  top: 58px;
  overflow-y: auto;
}

#autocomplete-list .list-group-item {
  cursor: pointer;
}

#autocomplete-list .list-group-item:hover {
  background-color: #3b52a3;
}

